import React, { useContext, useEffect } from "react";

import { UUID } from "@app/api-types/contracts";
import "@app/App.scss";
import { Jobs } from "@app/App/Jobs";
import { PdfUpload } from "@app/App/PdfUpload";
import { JobContext } from "@app/context-containers/Job";
import { UserContext } from "@app/context-containers/User";
import { FetchLevel } from "@app/types/main";

export function App() {
    // hooks
    const {
        state: {
            jobTypes,
            jobs: { list: jobs },
        },
        actions: { FetchGetJobtypes, FetchGetJobs },
    } = useContext(JobContext)!;
    const {
        state: { user, userAuth },
        actions: { FetchGenerateUser },
    } = useContext(UserContext)!;

    useEffect(() => {
        (async () => {
            if (jobTypes.fetchLevel === FetchLevel.initial) {
                await FetchGetJobtypes();

                return;
            }
        })();
    }, [jobTypes.fetchLevel]);

    useEffect(() => {
        (async () => {
            if (user.fetchLevel === FetchLevel.initial) {
                await FetchGenerateUser();

                return;
            }
        })();
    }, [user.fetchLevel]);

    // content rendering
    switch (jobTypes.fetchLevel) {
        case FetchLevel.success:
            break;
        default:
        case FetchLevel.fetching:
        case FetchLevel.initial:
            return (
                <p>
                    <em>Loading job types...</em>
                </p>
            );
    }

    switch (user.fetchLevel) {
        case FetchLevel.fetching:
            return (
                <p>
                    <em>Generating new demo user...</em>
                </p>
            );
        case FetchLevel.success:
            break;
        default:
        case FetchLevel.initial:
            return (
                <p>
                    <em>Loading...</em>
                </p>
            );
    }

    return (
        <div className="container" id="app">
            <div className="row">
                <div className="col pt-3">
                    <h1
                        className="text-primary"
                        onClick={() => {
                            (async () => {
                                if (userAuth === null) {
                                    return;
                                }

                                if ([FetchLevel.success, FetchLevel.failure].indexOf(jobs.fetchLevel) === -1) {
                                    return;
                                }

                                const ids = jobs.data.map<UUID>(v => v.id);
                                await FetchGetJobs(userAuth, ids, true);
                            })();
                        }}
                    >
                        PDF to XML
                    </h1>
                </div>
            </div>
            <PdfUpload />
            <Jobs />
        </div>
    );
}



