import { IJobDocument } from "@app/api-types/contracts/job";

export enum JobStatus {
    NOT_STARTED = "not_started",
    WAITING = "waiting",
    RUNNING = "running",
    CANCELLED = "cancelled",
    COMPLETED = "completed",
    FAILED = "failed",
}

export interface IJobProgress {
    current: number;
    total: number;
}

export interface IJob {
    id: string;
    status: JobStatus;
    progress: IJobProgress | null;
    documents?: IJobDocument[];
    output?: {
        uri: string;
    };
}



