import React, { useCallback, useContext, useEffect } from "react";

import { useDropzone } from "react-dropzone";

import { ChangeJobtype } from "@app/actions/job";
import { IFileReaderOptions } from "@app/api";
import { IErrors } from "@app/api-types/contracts";
import { ICreateJobResponse } from "@app/api-types/contracts/job";
import { IDocument } from "@app/api-types/entities/document";
import { IJobType } from "@app/api-types/entities/jobType";
import { ICreateDocumentRequest, ICreateDocumentResult } from "@app/api/v1/documents";
import { ICreateJobRequest, ICreateJobResult, JobInput } from "@app/api/v1/jobs";
import { JobContext, UserContext } from "@app/context-containers";

const renderOption = (jobType: IJobType, i: number) => (
    <option key={i} value={jobType.id}>
        {jobType.name}
    </option>
);

// drop-errors
const renderDropError = (key: string, value: string, i: number) => (
    <li key={i}>
        Error with file <strong>{key}</strong>: {value}
    </li>
);

const renderDropErrors = (errors: IErrors, opts?: IFileReaderOptions) => {
    if (Object.keys(errors).length === 0) {
        return null;
    }

    return (
        <>
            <p>Errors:</p>
            <ul>{Object.keys(errors).map((k: string, i: number) => renderDropError(k, errors[k], i))}</ul>
            {renderFilereaderOptions(opts)}
        </>
    );
};

// dropped-files
const renderDroppedFile = (file: File, i: number) => (
    <li key={i}>
        {file.name} ({(file.size / 1000 / 1000).toFixed(2)}MB)
    </li>
);

const renderDroppedFiles = (files: File[]) => {
    if (files.length === 0) {
        return null;
    }

    return (
        <>
            <p>Dropped files:</p>
            <ul>{files.map(renderDroppedFile)}</ul>
        </>
    );
};

// create-document requests
const renderCreateDocumentRequest = (request: ICreateDocumentRequest, i: number) => <li key={i}>{request.name}</li>;

const renderCreateDocumentRequests = (requests: ICreateDocumentRequest[]) => {
    if (requests.length === 0) {
        return null;
    }

    return (
        <>
            <p>Documents being created:</p>
            <ul>{requests.map(renderCreateDocumentRequest)}</ul>
        </>
    );
};

// create-document results
const renderCreateDocumentResultError = (key: string, value: string, i: number) => <li key={i}>{value}</li>;

const renderCreateDocumentResultErrors = (errors: IErrors) => {
    return <ul>{Object.keys(errors).map((k, i) => renderCreateDocumentResultError(k, errors[k], i))}</ul>;
};

const renderCreateDocumentResultDocument = (document: IDocument, i: number) => (
    <li key={i}>
        Document <strong>{document.name}</strong> was created successfully.
    </li>
);

const renderCreateDocumentResult = (result: ICreateDocumentResult, i: number) => {
    if (result.errors !== null) {
        return (
            <li key={i}>
                There were errors creating document <strong>{result.name}</strong>:
                {renderCreateDocumentResultErrors(result.errors)}
            </li>
        );
    }

    if (result.document !== null) {
        return renderCreateDocumentResultDocument(result.document, i);
    }

    return null;
};

const renderCreateDocumentResults = (results: ICreateDocumentResult[]) => {
    if (results.length === 0) {
        return null;
    }

    return (
        <>
            <p>Create document results:</p>
            <ul>{results.map(renderCreateDocumentResult)}</ul>
        </>
    );
};

// create-job requests
const renderCreateJobRequestInput = (input: JobInput) => {
    if ("document" in input) {
        return (
            <>
                File <strong>{input.document}</strong>
            </>
        );
    }

    if ("contents" in input) {
        return (
            <>
                Raw file <strong>{(input.contents.length / 1000 / 1000).toFixed(2)}MB</strong>
            </>
        );
    }

    return null;
};

const renderCreateJobRequest = (request: ICreateJobRequest, i: number) => (
    <li key={i}>{renderCreateJobRequestInput(request.input)}</li>
);

const renderCreateJobRequests = (requests: ICreateJobRequest[]) => {
    if (requests.length === 0) {
        return null;
    }

    return (
        <>
            <p>Jobs being created:</p>
            <ul>{requests.map(renderCreateJobRequest)}</ul>
        </>
    );
};

// create-job results
const renderCreateJobResultError = (key: string, value: string, i: number) => <li key={i}>{value}</li>;

const renderCreateJobResultErrors = (errors: IErrors) => {
    return <ul>{Object.keys(errors).map((k, i) => renderCreateJobResultError(k, errors[k], i))}</ul>;
};

const renderCreateJobResultResponse = (res: ICreateJobResponse, i: number) => (
    <li key={i}>
        Job <strong>{res.id}</strong> was created successfully.
    </li>
);

const renderCreateJobResult = (result: ICreateJobResult, i: number) => {
    if (result.errors !== null) {
        return (
            <li key={i}>
                There were errors creating job <strong>{renderCreateJobRequestInput(result.input)}</strong>:
                {renderCreateJobResultErrors(result.errors)}
            </li>
        );
    }

    if (result.response !== null) {
        return renderCreateJobResultResponse(result.response, i);
    }

    return null;
};

const renderCreateJobResults = (results: ICreateJobResult[]) => {
    if (results.length === 0) {
        return null;
    }

    return (
        <>
            <p>Create job results:</p>
            <ul>{results.map(renderCreateJobResult)}</ul>
        </>
    );
};

// file-reader options
const renderFilereaderOptions = (opts?: IFileReaderOptions) => {
    if (typeof opts === "undefined") {
        return null;
    }

    return (
        <>
            <p>File requirements:</p>
            <ul>
                {opts.sizeUpperLimit && <li>Files must be less than {opts.sizeUpperLimit / 1000 / 1000}MB</li>}
                {opts.mimetypeWhitelist && (
                    <li>Files must be of one of the following types: {opts.mimetypeWhitelist.join(", ")}</li>
                )}
            </ul>
        </>
    );
};

export function PdfUpload() {
    // job-context
    const {
        actions: { DropFiles },
        dispatch,
        state: {
            currentJobType,
            jobs: { createRequests: createJobRequests, createResults: createJobResults },
            jobTypes,
            documents: { dropped, createRequests: createDocumentRequests, createResults: createDocumentResults },
            fileReaderOptions,
        },
    } = useContext(JobContext)!;

    // user-context
    const {
        state: { userAuth },
    } = useContext(UserContext)!;

    // drop-zone functionality
    const onDrop = useCallback(
        (acceptedFiles: File[]) => {
            (async () => {
                if (currentJobType === null) {
                    return;
                }

                await DropFiles(userAuth!, acceptedFiles, currentJobType);
            })();
        },
        [currentJobType],
    );
    const { getRootProps, getInputProps } = useDropzone({ onDrop });

    useEffect(() => {
        if (jobTypes.data.length === 0) {
            return;
        }

        if (currentJobType !== null) {
            return;
        }

        dispatch(ChangeJobtype(jobTypes.data[0]));
    }, [jobTypes, currentJobType]);

    return (
        <>
            <div className="form-row mb-2">
                <div className="col pt-3">
                    <div className="dropzone-wrapper border-primary" {...getRootProps()}>
                        <i className="icon-arrow-up-circle mb-2" />
                        <h5 className="mb-2">Drag and drop files here or click to open the file dialog.</h5>
                        <span className="text-muted">PDF files only. Maximum 10MB in size.</span>
                        <input {...getInputProps()} />
                    </div>
                </div>
            </div>
            <div className="row mb-2">
                <div className="col pt-3">
                    <div className="form-group row">
                        <label className="col-form-label col-md-4">Job type</label>
                        <div className="col-md-8">
                            <select
                                className="custom-select"
                                onChange={e => {
                                    const selectedJobtype: IJobType | null = jobTypes.data.reduce<IJobType | null>(
                                        (out, v) => {
                                            if (out !== null) {
                                                return out;
                                            }

                                            if (v.id === e.target.value) {
                                                return v;
                                            }

                                            return null;
                                        },
                                        null,
                                    );
                                    if (selectedJobtype === null) {
                                        return;
                                    }

                                    dispatch(ChangeJobtype(selectedJobtype));
                                }}
                                value={currentJobType === null ? "" : currentJobType.id}
                            >
                                {jobTypes.data.map(renderOption)}
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            {renderDropErrors(dropped.errors, fileReaderOptions)}
            {renderDroppedFiles(dropped.files)}
            {renderCreateDocumentRequests(createDocumentRequests)}
            {renderCreateDocumentResults(createDocumentResults)}
            {renderCreateJobRequests(createJobRequests)}
            {renderCreateJobResults(createJobResults)}
        </>
    );
}



